import { useState } from "react";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Edit2, Users, Search, AlertCircle, CheckCircle2, Clock, XCircle, CalendarClock } from "lucide-react";
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from "@/components/ui/tooltip";
import { Alert, AlertDescription } from "@/components/ui/alert";

interface Soutenance {
  id: number;
  date: string;
  time: string;
  location: string;
  groupe: string;
  juryNames: string | string[];
  status: "Scheduled" | "Completed" | "Pending" | "Cancelled" | string;
}

interface SoutenanceTableProps {
  soutenances?: Soutenance[];
  onEdit?: (id: number) => void;
}

interface StatusConfig {
  color: string;
  bg: string;
  hoverBg: string;
  icon: React.ReactNode;
  text: string;
}

function getStatusConfig(status: string): StatusConfig {
  switch (status) {
    case "Scheduled":
      return {
        color: "text-blue-700 dark:text-blue-300",
        bg: "bg-blue-100 dark:bg-blue-900/50",
        hoverBg: "hover:bg-blue-200 dark:hover:bg-blue-900/70",
        icon: <CalendarClock className="w-3.5 h-3.5" />,
        text: "Scheduled",
      };
    case "Completed":
      return {
        color: "text-green-700 dark:text-green-300",
        bg: "bg-green-100 dark:bg-green-900/50",
        hoverBg: "hover:bg-green-200 dark:hover:bg-green-900/70",
        icon: <CheckCircle2 className="w-3.5 h-3.5" />,
        text: "Completed",
      };
    case "Pending":
      return {
        color: "text-yellow-700 dark:text-yellow-300",
        bg: "bg-yellow-100 dark:bg-yellow-900/50",
        hoverBg: "hover:bg-yellow-200 dark:hover:bg-yellow-900/70",
        icon: <Clock className="w-3.5 h-3.5" />,
        text: "Pending",
      };
    case "Cancelled":
      return {
        color: "text-red-700 dark:text-red-300",
        bg: "bg-red-100 dark:bg-red-900/50",
        hoverBg: "hover:bg-red-200 dark:hover:bg-red-900/70",
        icon: <XCircle className="w-3.5 h-3.5" />,
        text: "Cancelled",
      };
    default:
      return {
        color: "text-gray-700 dark:text-gray-300",
        bg: "bg-gray-100 dark:bg-gray-800",
        hoverBg: "hover:bg-gray-200 dark:hover:bg-gray-800/70",
        icon: <AlertCircle className="w-3.5 h-3.5" />,
        text: "Unknown",
      };
  }
}

function StatusBadge({ status }: { status: string }) {
  const config = getStatusConfig(status);

  return (
    <Badge
      variant="outline"
      className={`
        ${config.bg} 
        ${config.color} 
        ${config.hoverBg}
        border-0 
        font-medium 
        gap-1.5
        px-2.5 
        py-0.5 
        transition-colors
        duration-200
        inline-flex
        items-center
        cursor-default
        select-none
        shadow-sm
      `}
    >
      {config.icon}
      {config.text}
    </Badge>
  );
}

export default function SoutenanceTable({ 
  soutenances = [], 
  onEdit = (id) => console.log(`Edit soutenance ${id}`) 
}: SoutenanceTableProps) {
  const [searchTerm, setSearchTerm] = useState("");

  const filteredSoutenances = soutenances.filter(soutenance => 
    soutenance.groupe.toLowerCase().includes(searchTerm.toLowerCase()) ||
    (typeof soutenance.juryNames === "string" 
      ? soutenance.juryNames.toLowerCase().includes(searchTerm.toLowerCase())
      : soutenance.juryNames.some(name => name.toLowerCase().includes(searchTerm.toLowerCase())))
  );

  if (!soutenances || soutenances.length === 0) {
    return (
      <Card className="w-full">
        <CardContent className="pt-6">
          <Alert>
            <AlertCircle className="h-4 w-4" />
            <AlertDescription>
              Aucune soutenance disponible pour le moment
            </AlertDescription>
          </Alert>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card className="w-full">
      <CardHeader>
        <div className="flex items-center justify-between">
          <CardTitle>Soutenances</CardTitle>
          <div className="relative w-64">
            <Search className="absolute left-2 top-2.5 h-4 w-4 text-muted-foreground" />
            <Input
              placeholder="Rechercher..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="pl-8"
            />
          </div>
        </div>
      </CardHeader>
      <CardContent className="p-0">
        <div className="relative w-full overflow-auto">
          <Table>
            <TableHeader className="bg-muted/50">
              <TableRow>
                <TableHead className="w-[180px] font-semibold">Date</TableHead>
                <TableHead className="w-[100px] font-semibold">Heure</TableHead>
                <TableHead className="w-[150px] font-semibold">Lieu</TableHead>
                <TableHead className="w-[120px] font-semibold">Groupe</TableHead>
                <TableHead className="w-[200px] font-semibold">Jury</TableHead>
                <TableHead className="w-[120px] font-semibold">Statut</TableHead>
                <TableHead className="w-[100px] font-semibold">Actions</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {filteredSoutenances.map((soutenance) => (
                <TableRow 
                  key={soutenance.id} 
                  className="transition-colors hover:bg-muted/50"
                >
                  <TableCell className="font-medium">
                    {new Date(soutenance.date).toLocaleDateString("fr-FR", {
                      day: "numeric",
                      month: "long",
                      year: "numeric",
                    })}
                  </TableCell>
                  <TableCell>{soutenance.time}</TableCell>
                  <TableCell>{soutenance.location}</TableCell>
                  <TableCell>
                    <Badge variant="secondary" className="font-medium">
                      {soutenance.groupe}
                    </Badge>
                  </TableCell>
                  <TableCell>
                    <TooltipProvider>
                      <Tooltip>
                        <TooltipTrigger asChild>
                          <div className="flex items-center gap-2 cursor-pointer">
                            <Users className="h-4 w-4 text-muted-foreground" />
                            <span className="truncate max-w-[150px]">
                              {Array.isArray(soutenance.juryNames)
                                ? soutenance.juryNames.join(", ")
                                : soutenance.juryNames}
                            </span>
                          </div>
                        </TooltipTrigger>
                        <TooltipContent>
                          <p className="max-w-xs">
                            {Array.isArray(soutenance.juryNames)
                              ? soutenance.juryNames.join(", ")
                              : soutenance.juryNames}
                          </p>
                        </TooltipContent>
                      </Tooltip>
                    </TooltipProvider>
                  </TableCell>
                  <TableCell>
                    <StatusBadge status={soutenance.status} />
                  </TableCell>
                  <TableCell>
                    <TooltipProvider>
                      <Tooltip>
                        <TooltipTrigger asChild>
                          <Button
                            variant="ghost"
                            size="icon"
                            className="hover:bg-muted"
                            onClick={() => onEdit(soutenance.id)}
                          >
                            <Edit2 className="h-4 w-4" />
                            <span className="sr-only">Modifier la soutenance</span>
                          </Button>
                        </TooltipTrigger>
                        <TooltipContent>
                          <p>Modifier la soutenance</p>
                        </TooltipContent>
                      </Tooltip>
                    </TooltipProvider>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </div>
      </CardContent>
    </Card>
  );
}